body {
  color: #d8e1e9;
  background-color: #404852;
}

ul, li, a {
  list-style-type: none;
}

button {
  cursor: pointer;
}

img {
  width: 250px;
  margin: 0 auto;
  display: block;
}

.logo-space {
  height: 70px;
  text-align: center;
  background-color: #222;
  margin: auto;
}

.logo-space img {
  width: 300px;
  padding: 10px;
}

.search-form {
  text-align: center;
  background-color: #376bb9;
  margin-bottom: 20px;
}

.search-form input {
  border: 1px solid #008cff;
  border-radius: 2px;
  outline: none;
}

.search-form button {
  letter-spacing: 1.5px;
  color: #299ecc;
  background-color: #fff;
  border: none;
  border-radius: 2px;
  transition: all .5s;
}

.search-form button:hover, .search-form button:focus {
  color: #fff;
  background-color: #299ecc;
}

.gallery {
  max-width: 1200px;
  flex-wrap: wrap;
  margin: 0 auto;
  display: flex;
}

.photo-card {
  width: 250px;
  flex-direction: column;
  justify-content: space-between;
  margin: 5px auto;
  display: flex;
  box-shadow: -3px 3px 6px -3px #fff;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  letter-spacing: 1.5px;
  text-align: center;
  flex-direction: column;
  font-size: 12px;
  display: flex;
}

.load-more-space {
  text-align: center;
  background-color: #376bb9;
  margin-top: 20px;
}

.load-more {
  width: 150px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: #299ecc;
  background-color: #fff;
  border: none;
  border-radius: 2px;
  margin: auto;
  padding: 3px;
  font-size: 11px;
  transition: all .5s;
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #299ecc;
}

